      <van-skeleton title :row="5"   />
      <van-skeleton title :row="5"   />

    </div>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="getarticiefn"
        :immediate-check="false"
      >
        <div class="list" v-for="(item, index) in list">
          <!-- <router-link
          :to="{ path: '/detailed', query: { item_id:item._id,cate_id:loginfo?.uid} }"
          custom
          v-slot="{ navigate }"
        >
          <span @click="navigate" @keypress.enter="navigate" role="link"> -->
          <div @click="tiao(item._id)">
            <li v-show="item?.imageSrc.length== 0" class="zero">
              <h6>{{ item?.title, }}</h6>
              <p class="myp">
                <span>作者：{{ item?.author }}</span
                ><span>{{ item?.comment }}条评论</span
                ><span>{{ timeAgo(item?.time) }}发布</span>
              </p>
            </li>
            <li v-show="item?.imageSrc.length== 1" class="one">
              <div class="f">
                <div class="zuo">
                  <h6>{{ item?.title }}</h6>
                </div>
                <div class="myimg you">
                  <img :src="item?.imageSrc" @error="myerror" alt="" />
                </div>
              </div>
              <p>
                <span>作者：{{ item?.author }}</span
                ><span>{{ item?.comment }}条评论</span
                ><span>{{ timeAgo(item?.time) }}发布</span>
              </p>
            </li>
            <li v-show="item?.imageSrc.length == 3" class="three">
              <h6>{{ item?.title, }}</h6>
              <div class="myimg">
                <img :src="myimg" @error="myerror" alt="" v-for="myimg in item?.imageSrc" />
              
              </div>
              <p>
                <span>作者：{{ item?.author }}</span
                ><span>{{ item?.comment }}条评论</span
                ><span>{{ timeAgo(item?.time) }}发布</span>
              </p>
            </li>
          </div>
          <!-- </span>
        </router-link> -->
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { getcate } from "../api/home";
import { getarticie } from "../api/home";

import { timeago } from "../assets/time";

export default {
  name: "index",

  data() {
    return {
      active: 0,
      list: [],
      fenlei: [],
      loading: false,
      finished: false,
      limit: 10,
      loginfo: JSON.parse(sessionStorage.getItem("loginfo")),
      tf: true,
    };
  },
  computed: {
    cate_id() {
      // return    this.fenlei[this.active] && this.fenlei[this.active]._id
      return this.fenlei[this.active]?._id;
    },
  },
  watch: {
    cate_id() {
      this.list = [];
      this.getarticiefn();
    },
  },
  created() {
    this.fenleifn();
    this.gujiaFn();
  },
  methods: {
    tiao(item_id) {
     if(this.loginfo==null){
         this.$dialog
        .confirm({
          title: "警告警告··········警告警告",
          message: "小伙子，没有登录还想进入详情页？？？？想什么呢？怎么可能呢嘿嘿嘿",
        })
        .then(() => {
          this.$router.push("/login");
        })
        .catch(() => {
          this.$toast("你小子取消是吧，那你别想进去了！！！！");
          this.$notify({
            type: "danger",
            message: "你小子取消是吧，那你别想进去了！！！！",
          });
        });
     }else{
       
       this.$router.push({name:'detailed',query:{
          item_id:item_id,cate_id:this.loginfo?.uid
          }})
     }
    },
    gujiaFn() {
      setTimeout(() => {
        this.tf = false;
      }, 1500);
    },
    timeAgo(val) {
      return timeago(val);
    },
    // mylength(index) {
    //   return this.list[index].imageSrc.length;
    // },
    myerror(e) {
      e.target.src = require("../assets/error.png");
    },
    fenleifn() {
      getcate()
        .then((result) => {
          this.fenlei = result.data.data;
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    getarticiefn() {
      getarticie({
        cate_id: this.cate_id,
        skip: this.list.length,
        limit: this.limit,
      })
        .then((result) => {
          this.list = this.list.concat(result.data.data);

          this.loading = false;
          if (this.list.length == result.data.count) {
            this.finished = true;
            this.$toast("没有更多了");
            this.$notify({ type: "success", message: "没有更多了" });
          }
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
  },
};
</script>


